.table-hero {
    font-family: $m-500;
    height: 5rem;
    display: flex;
    align-items: center;
    align-content: center;
    padding-left: 4rem;
    font-family: $m-500;
    color: $color-table-hero;
    margin-top: 4rem;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -2rem;
        background-color: $color-line;
        width: 100%;
        height: 1px;
    }

    &__title {
        flex: 1;
    }

    


}

.material-icons {
    font-size: 2rem;
}

.vs-collapse .vs-collapse-item header .icon-header {
    top: 70%;
    right: 2%;
}
